
section.software {
    background-color: #000000;
    min-height: 100vh;
}

.software-title-wrapper {
    display: flex;
    position: relative;
    top: -133px;
    flex-flow: row-reverse;
}

.software-title {
    color: white;
    font-weight: bold;
    /*margin-bottom: 1rem;*/
    text-align: right;
    padding-right: 6.4vw;
    width: 662px;
    background: black;
    zoom: .5;
    /*width: 481px;*/
}
.scroll-down {
    width: 73px;
    background: #1c1c1c;
    display: flex;
    justify-items: center;
    justify-content: center;
}

.scroll-down>img{
    width: 80%;
}

.scroll-down:hover {
    background: #000000b0;
    cursor: pointer;
}

.tiles {
    display: grid;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
    grid-auto-rows: minmax(40vh, auto);
}

.tile {
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgb(0 0 0 / 10%);
    margin: 1vw;
    overflow: hidden;
    max-height: 40vh;
    position: relative;
    filter: grayscale(100);
    cursor: pointer;
}

.tile:hover {
    filter: grayscale(0);

}

.tile > img {
    width: 135%;
    height: 415px;
}

.tile > img:hover {
    scale: 1.1;
}

.tile-title {
    font-weight: bold;
    font-size: 2vh;
    text-align: center;
    width: 100%;
    bottom: 0;
    position: absolute;

    padding: 2.4vh 0;
    background: #000000b0;
    color: white;
    backdrop-filter: blur(13px);
    transition: all 0.3s ease;
}

@media (max-width: 1024px) {
    .tiles {
        grid-template-columns: repeat(1, minmax(300px, 1fr));
    }

    .tile {
        max-width: 80vh;
    }
}

.badge {
    width: 70px;
    height: 100px;
    background: red;
    position: absolute;
    color: white;
    margin-left: 11px;
    border-radius: 1px;
}

